import React from "react";
import { CInput } from "@components";
import { Col, Empty, Form, Row } from "antd";

const CAssetType = ({ isForms, isEmpty, handleChange, forms, errors }) => {
  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 4 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 20 },
    },
  };
  return (
    <>
      {isEmpty && <Empty />}
      {isForms && (
        <>
          <Row>
            <Col span={24}>
              <Form {...formItemLayout}>
                <CInput
                  label="Code"
                  validate={errors.code}
                  name="code"
                  id="code"
                  value={forms.code}
                  onChange={handleChange}
                  autoFocus
                />
                <CInput
                  value={forms.name}
                  label="Name"
                  validate={errors.name}
                  name="name"
                  id="name"
                  isRequired
                  onChange={handleChange}
                  style={{ marginTop: -20 }}
                />
              </Form>
            </Col>
          </Row>
        </>
      )}
    </>
  );
};

export default CAssetType;
